:root
    --primary: #6200EE
    --primary-variant: #3700B3
    --primary-variant-2: #BB86FC
    --secondary: #E65100
    --secondary-variant: #FFB74D
    --background-light: #FFFFFF
    --background-dark: #333333
    --font-light: #333333
    --font-dark: #dddddd
    --error: #B00020

@font-face
    font-family: myFont
    src: url(../fonts/Montserrat-Regular.ttf)
    font-weight: normal


@font-face
    font-family: myFontMedium
    src: url(../fonts/Montserrat-Medium.ttf)
    font-weight: normal


*
    font-family: myFontMedium
    transition-duration: 200ms



#content
    background-color: white
    padding: 30px
    width: 40%
    border-radius: 5px
    border: solid 1px #b6b6b6
    box-shadow: rgba(0, 0, 0, 0.5) 0px 2px 10px
    text-align: center

input[type="text"]
    background: #e6e6e6
    border: none
    border-bottom: solid 2px var(--primary-variant-2)
    border-radius: 5px
    height: 48px
    padding: 0 12px 0 16px
    margin: 10px


input[type="text"]:focus 
    border-bottom: solid 2px var(--primary)
    outline: none

input[type="button"]
    min-width: 64px
    height: 36px
    color: white
    font-family: myFontMedium
    background: var(--primary)
    border-radius: 5px
    border: solid 1px #b6b6b6
    box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 5px
    padding: 0 16px
    margin: 10px
.base 
    width: 40px
